@font-face{
	font-family: Rubik-Bold;
	src: url(./fonts/Rubik/Rubik-Regular.ttf);
}
@font-face{
	font-family: Muli;
	src: url(./fonts/Muli/static/Muli-Medium.ttf);
}

.App {
  text-align: center;
  
}

.App-header{
	background-color: rgb(16, 201, 130);
	height:80px;
	box-shadow:0 8px 10px rgb(0, 0, 0,0.2);
	
	background-color:black;
	color:white;
	
}

.App-header .logo{
	text-align:left;
	float:left;
	padding-left:40px;
	padding-top:10px;
	font-size:32px;
	font-family:Muli;
	font-weight:800;
	
}
.App-header .logo:hover{
	cursor:pointer;
}

.logo a{
	text-decoration: none;
	color:white;
}

.logo a:visited{
	color:white;
	
}

.footer{

   position:relative;
   bottom:0;
   width:100%;
   height:100px;   
   background:black;
   color:grey;
   font-size:16px;
   font-family:"Muli";

   
	
}

	
.footer_a:hover{
	cursor:pointer;
}

.footer_a{
	display:inline-block;
	padding-left:30px;
	top:50px;
	position:relative;
	margin:0 auto;
}

.topMenu{
	position:relative;
	left:-40px;
}

.cart{
	position:relative;
	top:20px;
	float:right;
	right:40px;

}

.cart img{
	width:42px;
	
}

.cart img:hover{
	width:48px;
	position:relative;
	top:-2px;
	margin-right:-6px;
	cursor:pointer;
}

.navibar a{
	text-decoration: none;
}
.navibar a:link{
	color:white
}
.navibar a:visited{
	color:white
}
.navibar a:hover{
	color:grey
}


.navibar{
	top:20px;
	float:right;
	left:-450px;
	position:relative;

}
.cat{
	margin-left:100px;
	position:relative;
	top:3px;
	font-size:20px;
	font-family:Rubik-Bold;
	display:inline-block;
	padding-top:5px;


}



.cat:hover{
	color:#f8f9fa;
	cursor:pointer;
}

.searchtool{
	color:black;
	position:relative;
	float:right;
	left:500px;
	top:12px;
	max-width:242px;
}

.searchBar2{
	position:absolute;
	float:right;
	right:560px;
	top:6px;
	
	width:40px;
	height:40px;
	background-color:#473051;
	
	background-color:white;
	color:black;
	border:1px solid #001f3f;
	
	border-radius:20px;
	width:240px;
	display:block;
	

}

.searchBar{
	position:absolute;
	float:right;
	
	top:6px;
	right:-50px;
	width:40px;
	height:40px;
	background-color:#473051;
	
	background-color:white;
	color:black;
	border:1px solid #001f3f;
	
	border-radius:20px;
	width:240px;
	


}


.searchBar:hover{
	width:240px;
	transition:0.4s;
}

.searchBar:hover > input {

   display:block;
}



.searchBar input{
	height:27px;
	max-width:170px;
	outline-width:none;

	padding-left:8px;
	position:absolute;
	left:47px;
	background-color:transparent;
	top:6px;
	border:none;
	color:black;
	font-size:16px;
	display:block;
	color:black;
	
}

.searchBar input:focus{
	outline: none;
	
}


.searchBar img{
	top:9px;
	position:absolute;
	left:8px;
	width:26px;
}


.style-line{
	display:inline-block;
	height:30px;
	width:1px;
	background-color:white;
	position:relative;
}