.cpanel{
	position:relative;
	height:1300px;
	background-color:#f8f9fa;
}

.leftNavi{
	background-color:#DDDDDD;
	float:left;
	display:inline-block;
	height:100%;
	width:100px;
	z-index:3
}

.cpanel_btn{
	position:relative
}


.cpanel_btn img{
	width:62px;
	margin-top:50px;
	
}

.cpanel_btn img:hover{
	position:relative;
	width:68px;
	margin-top:44px;
	cursor:pointer;
	
	

}

.panel{
	position:absolute;
	color:black;
	display:inline-block;
	text-align:left;
	float:left;
	max-width:100%;
	left:100px;
	z-index:1
	
}


.products_manager{
	font-size:26px;
	font-family:"Muli";
	font-weight:600;
	position:relative;
	width:100%;
	z-index:1;

	
}

.title_panel{
	border-bottom:1px solid black;
	font-size:26px;
	max-width:100%;
	height:50px;
	padding-top:20px;
	padding-left:20px;
	background-color:black;

	color:white;
	border-top:2px solid white;
	margin-top:20px;

	
}

.option1{
	margin-left:20px;
	margin-top:50px;
	
	
}

.option2{
	margin-left:20px;
	margin-top:50px;
	
	
}

.option3{
	margin-left:20px;
	margin-top:50px;
	width:1000px;
	
	
}

.option3 select{
	width:100px;
	font-size:12px;
	height:30px;
}


.option1 select{
	width:200px;

	margin-top:20px;
}


.panel button{

	position:relative;
	width:130px;
	height:35px;
	background-color:#182C61;
	left:5px;
	margin-top:15px;
	font-family:"Muli";
	font-weight:600;
	font-size:16px;
	color:white;
	padding-top:5px;
	padding-bottom:5px;
	display:inline-block;
}

.panel input{

	position:relative;
	width:130px;
	height:30px;
	margin-left:0px;

  padding: 7px 12px;


  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;

}

.option1 input{
	margin-left:40px;
}
.option2 input{
	margin-left:0px;
}


.option3 table{
	font-size:12px;
}

.option3_input_tab{
	font-size:14px;
	font-family:"arial";
	width:130px;
	font-weight:400;
	display:inline-block;
	margin-left:20px;
	left:-20px;
	position:relative;
	
}

.option3_input_tab input{
	border-radius:0px;
	height:30px;
}

table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 800px;
  height:630px;
  overflow-y: scroll;
  display: block;
}

td, th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #dddddd;
}

.t1{
	font-size:14px;
}
