@font-face{
	font-family: Cairo-Light;
	src: url(./fonts/Cairo/Cairo-Light.ttf);
}



h1{
	font-family:Muli;
	font-weight:800;
	font-size:42px;

}


.searchBox{
	height:30px;
	
}

.Featured{
	background-color:#f2f2f2;
	position:relative;
	padding-top:10px;
	padding-bottom:40px;
	top:0px;
	box-shadow:2px 2px 2px rgb(0,0,0,0.2);
	border-bottom:2px solid white;
	min-height:800px;




}
.featuredBox{
	width:90%;
	margin:0 auto;
}


.Categories{
	position:relative;
	top:70px;
	height:600px;
	

}

.Categories img{
	width:241px;
	height:241px;
}


.Categories h1{
	font-size:48px;
}
.Category{
	
	position:relative;
	top:80px;
	
}


.product{
	position:relative;
	width:301px;
	height:307px;
	background-color:#5A5557;
	
	display:inline-block;
	margin-left:80px;
	margin-top:40px;
	left:-35px;
	border:2px solid #edeff2;
	text-align:left;
	border-bottom-right-radius: 5px;
	border-bottom-left-radius: 5px;
	color:white;
	
	
	background-color:black;
	background-color:white;
	
	border:2px solid #edeff2;
#-webkit-box-shadow: 0px 2px 28px 17px rgba(0,0,0,0.75);
#-moz-box-shadow: 0px 2px 28px 17px rgba(0,0,0,0.75);
#box-shadow: 0px 2px 28px 11px rgba(0,0,0,0.75);


-webkit-box-shadow: 0px 2px 28px 17px rgba(0,0,0,0.75);
-moz-box-shadow: 0px 2px 28px 17px rgba(0,0,0,0.75);
box-shadow: 0px 2px 10px 1px rgba(0,0,0,0.75);
	

	animation: pop2 0.5s linear 1;
	
	
}

.Featured .product{
	animation: pop2 0.5s linear 1;
}

@keyframes pop2{

  0%  {opacity: 0.1;}
  30%  {opacity: 0.3;}
  50%  {opacity: 0.5;}
  70%  {opacity: 0.7;}
  100%  {opacity: 1;}
}


.product img{
	border-bottom:1px solid rgb(90,85,87,0.2);
	width:221px;
	height:221px;
	left:40px;
	position:relative;
	z-index:2;
}

.product:hover{
	border:2px solid black;
	cursor:pointer;
}

.productDetail{
	
color:black;
}


.prodName{
	position:relative;
	left:10px;
	font-family:Cairo-Light;
	font-weight:600;
	font-size:23px;
}

.prodPrice{
	position:relative;
	left:10px;
	font-family:Cairo-Light;
	font-weight:600;
	margin-top:-7px;
}


.logoimg{
	height:100px;
	width:100%;
	margin:0;
	overflow:hidden;
	margin:0 auto;



}
.logoimg img{
	
	display:block;
	width: 100%;
	/* box-shadow:5px 5px 8px 5px rgb(0,0,0,0.4); */
	margin:0 auto;
	border-top:2px solid white;
	




}


.category{
	width:391px;
	height:272px;
	margin:0 auto;
	background-color:white;
	font-family:Muli;
	font-weight:600;
	font-size:25px;
	display:inline-block;
	position:relative;
	margin-left:80px;
	left:-35px;

	border:2px solid #edeff2;
-webkit-box-shadow: 0px 2px 28px 17px rgba(0,0,0,0.75);
-moz-box-shadow: 0px 2px 28px 17px rgba(0,0,0,0.75);
box-shadow: 0px 2px 20px 5px rgba(0,0,0,0.75);
	
}

.category:hover{
	border:2px solid orange;
	cursor:pointer;
	
}

#overlay {
  position: fixed; /* Sit on top of the page content */
  display: block; /* Hidden by default */
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5); /* Black background with opacity */
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer; /* Add a pointer on hover */
}
