.Item{
	position:relative;
	height:1300px;
	background-color:#f8f9fa;
	
	

}


.item_data{

	width:370px;
	height:630px;
	margin:0 auto;
	top:-40px;
	left:-350px;
	position:relative;
}

.item_border{
	
	z-index:3;
	position:fixed;
	background-color:#f8f9fa;
	border:1px solid black;
-webkit-box-shadow: 0px 2px 28px 17px rgba(0,0,0,0.75);
-moz-box-shadow: 0px 2px 28px 17px rgba(0,0,0,0.75);
box-shadow: 0px 2px 28px 17px rgba(0,0,0,0.75);

	top:70px;
	margin:0 auto;
	left: 50%;
	top:50%;
	
	width:850px;
	margin-left: -500px;
	height:600px;
	margin-top: -340px;
	padding-left:200px;
}

.itemPhoto{

	width:370px;
	height:370px;
	margin: 0 auto;
	top:100px;
	position:relative;
}


.itemPhoto img{
	width:366px;
	height:366px;
	
	border:1px solid grey;
}



.otherpics{
	position:relative;
	top:-251px;
	float:left;

	
}
.otherpics img{
	height:120px;
	width:120px;
	
	cursor:pointer;
	border:1px solid grey;
	margin-left:1px;
}

.itemInfo{
	position:relative;
	left:490px;
	height:350px;
	top:-270px;
	text-align:left;
	vertical-align: top;
}


.item_title{
	font-family:"Muli";
	font-size:36px;
	border-bottom:1px solid black;
	padding-bottom:5px;
}

.item_cat{
	margin-top:10px;
	font-family:"Muli";
	color:grey;
	
}

.item_price{
	margin-top:5px;
	font-family:"Rubik-Bold";
	color:black;
	font-weight:400;
	font-size:28px;
	
}

.item_avaiable{
	font-family:"Muli";
	color:green;

}

.item_qty{
	position:relative;
	top:40px;
	font-size:20px;
	 font: 400 17px/2em 'Source Sans Pro', sans-serif;
	
}

.item_size{
	position:relative;
	top:60px;
	font-size:20px;
	 font: 400 17px/2em 'Source Sans Pro', sans-serif;
	
}

.itemInfo button{
	position:relative;
	top:170px;
}

select{
	margin-left:10px;
  width: 50%;

  padding: 5px 0;
  height: 40px;

  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  background: none transparent;
  border: 0 none;
  background: #ececec;
  font: 400 17px/2em 'Source Sans Pro', sans-serif;
	
}

.Item2 button{
background-color:#182C61;	
	position:relative;
	width:400px;
	height:50px;
	background-color:#182C61;
	left:-40px;
	margin-top:0px;
	font-family:"Rubik-Bold";
	font-weight:300;
	font-size:23px;
	color:white;
	padding-top:5px;
}
